import React, { useState, useEffect } from "react";
import { Swiper, SwiperSlide } from "swiper/react/swiper-react.js";
import { Pagination, EffectCoverflow } from "swiper";

import "swiper/swiper.scss";
import "swiper/modules/pagination/pagination.scss";
import "swiper/modules/effect-coverflow/effect-coverflow.scss";
import "./Awards.css";

function Awardsnew(props) {
  useEffect((_) => {
    props.setPage("awards");
  }, []);

  return (
    <section className={"awards" + " " + props.className}>
      <Swiper
        effect={"coverflow"}
        grabCursor={true}
        centeredSlides={true}
        breakpoints={{
          0: {
            slidesPerView: 1,
            initialSlide: 0,
          },
          1200: {
            slidesPerView: 3,
            initialSlide: 1,
          },
        }}
        coverflowEffect={{
          rotate: 50,
          stretch: 0,
          depth: 100,
          modifier: 1,
          slideShadows: true,
        }}
        pagination={true}
        modules={[EffectCoverflow, Pagination]}
        className="swiper-container"
      >
        {props.displayText.map((properties) => {
          return (
            <SwiperSlide>
              <SingleAward
                key={properties.key}
                srcTrophy={properties.srcTrophy}
                title={properties.title}
                firstPartSubtitle={properties.firstPartSubtitle}
                secondPartSubtitle={properties.secondPartSubtitle}
                firstPartComment={properties.firstPartComment}
                secondPartComment={properties.secondPartComment}
                date={properties.date}
                srcCertificate={properties.srcCertificate}
                urlCertificate={properties.urlCertificate}
              />
            </SwiperSlide>
          );
        })}
      </Swiper>
    </section>
  );
}

function SingleAward(props) {
  const [loaded, setLoaded] = useState(false);
  return (
    <div className="swiper-slide-caption">
      <img src={props.srcTrophy} alt="trophy-icon" className="awards-icon" />
      <h1>{props.title}</h1>
      <h2>{props.firstPartSubtitle}</h2>
      <h2>{props.secondPartSubtitle}</h2>
      <p>
        {props.firstPartComment}
        <br />
        {props.secondPartComment}
      </p>
      <h3>{props.date}</h3>
      <div>
        {!loaded ? <h1 className="loading">Loading Certificate...</h1> : null}
      </div>
      <a href={props.urlCertificate} target="_blank">
        <img
          src={props.srcCertificate}
          alt="certificate"
          className="certificate-img"
          style={!loaded ? { visibility: "hidden" } : {}}
          onLoad={() => setLoaded(true)}
        />
      </a>
    </div>
  );
}

export default Awardsnew;
